* {
  margin: 0;
  padding: 0;
  border: 0;
}

.mainClass {
  margin: 0px;
  background-image: linear-gradient(to bottom right, #142850, #27496d);
  color: #838383;
  width: 100vw;
  height: 95vh;
  border-bottom: 1px solid #0c7b93;
  .titleText {
    position: fixed;
    width: 100vw;
    height: 15%;
    line-height: 70%;
    margin-top: 1%;
    margin-left: 1%;
    border-bottom: 1px solid #0c7b93;
    h1 {
      padding-top: 15px;
      color: #0044ff;
    }
    h2 {
      padding: 15px;
      color: #00d0ff;
    }
  }
  .personalBranding {
    padding-top: 10%;
    padding-left: 25%;
    text-align: center;
    width: 50%;
  }
}

footer {
  background-image: linear-gradient(to bottom right, #142850, #27496d);
}
